.App {
  text-align: center;  
}
.container{
  padding: 50px 50px;
}
.jumbotron
{
	position: relative;
  background: #000 url("header.jpg") center center;
    width: 100%;
    height: 100%;
    background-size: cover;
    overflow: hidden;	    
    opacity: 0.8;    
    margin: 0;
}
.jumbotron p
{
	color: #09c;
	text-shadow: 1px 1px #333, 
                2px 2px #333, 
                3px 3px #333;
    font-size: 28pt;
    font-family: 'Indie Flower', cursive;
}
.jumbotron h1
{	
	color: #c00;
	text-shadow: 1px 1px #333, 
                2px 2px #333, 
                3px 3px #333;
    font-size: 72pt;
    font-family: 'Anton', sans-serif;
}
h4,h2{
  font-family: 'Anton', sans-serif;
}
p{  
  font-family: 'Anton', sans-serif;
  font-size: x-large;
}
img{
  opacity: 0.8;
}
p a{
  text-decoration: none;
  color: black;
}